.bar-chart{
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    width: calc(100% - 5px);
    height: calc(100% - 40px);
}

.legend-container{
    display: flex; 
    justify-content: center;
    margin-top: 20px;
}

.legend-items{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 20px;
}